* {
  margin: 0;
  padding: 0;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #000;
}

.type-selecter {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.type-selecter label {
  vertical-align: top;
}

.type-selecter button {
  background: #999;
}
 
.type-selecter button, .type-selecter p {
  width: 30px;
}

.modal-page {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  height: 100vh;
  max-width: 400px;
  height: calc(100vh - 10px);
  padding: 5px;
  margin: auto;
}

.forms {
  transform-origin: center;
  padding: 5%;
}

.open {
  stroke: white;
  fill: white;
  color: white;
}

.close {
  stroke: #666;
  fill: #666;
  color: #666;
}

.drinks {
  overflow: auto;
  height: 70px;
  width: 90vw;
  max-width: calc(400px - 10%);
  margin: 0 5%;
  margin-bottom: 5px;
}

.drink-scroll {
  display: inline-flex;
  flex: 1;
}

.drink {
  white-space: nowrap;
  float: left;
  width: 40px;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
}


.settings {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: calc(100vh - 60px);
  padding: 5px;
}

.close-button {
  background: #993344;
  font-weight: 600;
  width: fit-content;
  padding: 5px;
  display: block;
}

.confirm-button {
  font-weight: 900;
  background: #4CAF50;
  padding: 5px;
}

.App {
  height: 100vh;
  background: #111;
  text-align: center;
  display: grid;
  grid-template-rows: calc(100vh - 60px) 60px;
}

.history {
  overflow: auto;
  color: #fff;
  height: calc(100vh - 150px);
}

.page {
  height: 100%;
  width: 100%;
  max-width: 400px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.nav {
  height: 60px;
  width: 100%;
  margin: auto;
  background: #111;
  border-top: 2px #444 dotted;
  
}

.nav button {
  height: 60px;
  width: 60px;
  background: transparent;
  font-weight: 600;
  transition: all 30ms ease-in;
}

body {
  background: #000;
  height: 100vh;
  overflow: hidden;
  width: 100%; 
  max-width: 400px;
  margin: auto;
}

label {
  color: aliceblue;
}

canvas {
  width: 90%;
  max-width: 400px;
  max-height: calc(100vh - 180px);
}

.graph-nav {
  background: #fff;
  width: 90%;
  max-width: 400px;
  display: flex;
  justify-content: space-around;
  margin: auto;
}

.add-button {
  width: 50%;
  font-weight: 900;
  background: #4CAF50;
}

h3 {
  color: #000;
  width: 50%;
}

h1 {
  color: #fff;
  border-bottom: 2px #444 solid;
  width: 100%; 
  max-width: 400px;
  margin: auto;
}

.slidecontainer {
  width: 100%; /* Width of the outside container */
  max-width: 400px;
  margin: auto;
}

/* The slider itself */
.slider {
  -webkit-appearance: none;  /* Override default CSS styles */
  appearance: none;
  width: 100%; /* Full-width */
  height: 25px; /* Specified height */
  background: #999; /* Grey background */
  outline: none; /* Remove outline */
  opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */
  -webkit-transition: .2s; /* 0.2 seconds transition on hover */
  transition: opacity .2s;
}

/* Mouse-over effects */
.slider:hover {
  opacity: 1; /* Fully shown on mouse-over */
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  width: 25px; /* Set a specific slider handle width */
  height: 25px; /* Slider handle height */
  background: #4CAF50; /* Green background */
  cursor: pointer; /* Cursor on hover */
}

.slider::-moz-range-thumb {
  width: 25px; /* Set a specific slider handle width */
  height: 25px; /* Slider handle height */
  background: #4CAF50; /* Green background */
  cursor: pointer; /* Cursor on hover */
}

button {
  border: none;
}

button:focus {
  outline: none;
}


